* {
    margin: 0;
    padding: 0px;
    box-sizing: border-box;
}

body {
    font-family: 'montseratt', sans-serif;
}

.app {
    background-image: url('./assets/backg.jpg');
    background-size: cover;
    background-position: center;
}

main {
    min-height: 100vh;
    padding: 25px;
}

.Greet {
    text-align: center;
    margin: 0px 0px 70px;
    display: block;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 16px 5px;
    font-size: 35px;
    color: #FFF;
    font-style: italic;
}

.search-label {
    border-radius: 0px 0px 16px 5px;
    font-size: 20px;
    color: #FFF;
    font-style: italic;
    width: 40%;
}

.search-box {
    width: 40%;
    margin: 10px 0px 175px;
}

.search-box .search-bar {
    display: inline-block;
    width: 70%;
    appearance: none;
    outline: none;
    border: none;
    background: none;
    border-radius: 0px 0px 0px 5px;
    margin-top: -25px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
}

.search-button {
    position: relative;
    display: inline-block;
    font-size: 20px;
    width: 18%;
    color: #313131;
    cursor: pointer;
    font-style: italic;
    border-radius: 0px 0px 5px 5px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
}

.search-box .search-button:hover {
    background-color: rgba(255, 255, 255, 0.75);
}

.search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
    color: #FFF;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    transition: 0.4s ease;
}

.location-box .date {
    color: #FFF;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    transition: 0.4s ease;
}

.weather-box {
    text-align: center;
}

.weather-box .temp {
    position: relative;
    display: inline-block;
    margin: 30px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    font-size: 102px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50, 50, 50, 0.75);
    text-align: center;
    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
    color: #FFF;
    transition: 0.4s ease;
}

.weather-box .weather {
    color: #FFF;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
    transition: 0.4s ease;
}